import React, { Component } from 'react';

import UserInfo from './User/UserInfo';
import UserProgress from './User/UserProgress';

import Ratings from './Ratings/Ratings';
import RatingsDisclaimer from './Ratings/RatingsDisclaimer';
import RatingsSettings from './Ratings/RatingsSettings';
import RatingsTop from './Ratings/RatingsTop';

export default class Main extends Component {

	beautifyCount(count) {
		count = String(count).split('').reverse();
		count = count.map((num, index) => index % 3 === 0 ? num + ' ' : num);
		count = count.reverse().join('').trim();

		return count
	}
	
	render() {
		return (
			<main>
				<div className = 'container'>
					<div className = 'row'>
						<div className = 'col-md-4'>
							<UserInfo />
							<UserProgress />
							<Ratings beautifyCount = { this.beautifyCount } />
						</div>

						<div className = 'col-md-8'>
							<RatingsDisclaimer beautifyCount = { this.beautifyCount } />
							<RatingsSettings />
							<RatingsTop beautifyCount = { this.beautifyCount } />
						</div>
					</div>
				</div>
			</main>
		)
	}
}