import React, { Component } from 'react';

export default class RatingsSettingsType extends Component {

	render() {
		const { types, arrowSvg } = this.props;

		return (
			<div className = 'ratings-settings-type'>
				{ types.map((type, index) => {
					if (index === 0) {
						return <span key = { index } className = 'ratings-settings-type-item'>
							       { type }

							       <svg width = '10'
							        	height = '7'
							        	dangerouslySetInnerHTML = {{
							               __html: `<use xlink:href="${arrowSvg}#arrow" />` 
							        	}} />
							   </span>
					}

					return <span key = { index } className = 'ratings-settings-type-item'>{ type }</span>
				}) }
			</div>
		)
	}
}