import React, { Component } from 'react';

import RatingsTopUsersTableRow from './RatingsTopUsersTableRow';

export default class RatingsTopUsers extends Component {

	state = {
		tableHeaders: [
			{ name: 'place', text: '#' },
			{ name: '', text: '' },
			{ name: 'name', text: 'Участник' }, 
			{ name: 'age', text: 'Возраст' },
			{ name: 'city', text: 'Город' },
			{ name: 'points', text: 'Очки' }
		]
	}

	render() {
		const { tableHeaders } = this.state;
		const { users, beautifyCount } = this.props;
	
		return (
			<div className = 'ratings-top-users'>
				<table className = 'ratings-top-users-table'>
					<thead className = 'ratings-top-users-table-head'>
						<tr className = 'ratings-top-users-table-head-line'>
							{ tableHeaders.map((header, index) => {
							      return (
								      <th key = { index } className = { `item ${header.name}` }>
								          { header.text }
								      </th>
								  )
							}) }
						</tr>
					</thead>

					<tbody className = 'ratings-top-users-table-body'>
						{ users.map((user, index) => {
							return <RatingsTopUsersTableRow key = { index }
															place = { index + 1 }
															name = { user.name }
															age = { user.age }
															city = { user.city }
															points = { user.points }
															beautifyCount = { beautifyCount } />
						}) }
					</tbody>
				</table>
			</div>
		)
	}
}