import React, { Component } from 'react';

const penSvg = require('../../../img/main/pen.svg');
const userPhoto = require('../../../img/user-photo.jpg');

export default class UserInfoMain extends Component {

	state = {
		name: 'Катя Морозова',
		email: '@morozovakate'
	};

	render() {
		const { name, email } = this.state;

		return (
			<div className = 'user-info-main'>
				<a href = '/' 
				   className = 'user-info-main-change'
				   title = 'Редактировать профиль'>
				    <svg width = '10'
						 height = '10'
						 dangerouslySetInnerHTML = {{
						     __html: `<use xlink:href="${penSvg}#pen" />` 
						 }} />
				</a>
				<img className = 'user-info-main-img' 
					 src = { userPhoto }
					 alt = 'Фотография пользователя'/>
				<span className = 'user-info-main-name'>{ name }</span>
				<span className = 'user-info-main-email'>{ email }</span>
			</div>
		)
	}
}