import React, { Component } from 'react';

import Menu from './Menu';
import SearchArea from '../SearchArea';

const logoSvg = require('../../img/header/logo.svg');
const arrowSvg = require('../../img/header/arrow.svg');

const userPhoto = require('../../img/user-photo.jpg');

export default class Header extends Component {
	
	render() {
		return (
			<header>
				<div className = 'container'>
					<div className = 'row'>
						<div className = 'col-md-5'>
							<Menu />
						</div>

						<div className = 'col-md-5'>
							<div className = 'search'>
								<a href = '/'>
									<div className = 'logo'>
										<svg width = '24'
											 height = '34'
											 dangerouslySetInnerHTML = {{
											 	__html: `<use xlink:href="${logoSvg}#logo" />` 
											 }} />
									</div>
								</a>

								<SearchArea />
							</div>
						</div>

						<div className = 'col-md-2'>
							<div className = 'profile'>
								<div className = 'profile-info'>
									<span className = 'profile-info-name'>Катя</span>
									<img className = 'profile-info-img' 
										 src = { userPhoto } 
										 alt = 'Фото пользователя' />
									<svg width = '8'
										 height = '5'
										 dangerouslySetInnerHTML = {{
										 	__html: `<use xlink:href="${arrowSvg}#arrow"/>` 
										 }} />
								</div>

								<div className = 'profile-menu'>
									<span className = 'profile-menu-item'>
										<a href = '/'>Профиль</a>
									</span>

									<span className = 'profile-menu-item'>
										<a href = '/'>Лента</a>
									</span>

									<span className = 'profile-menu-item'>
										<a href = '/'>Профиль</a>
									</span>

									<span className = 'profile-menu-item'>
										<a href = '/'>Профиль</a>
									</span>

									<span className = 'profile-menu-item'>
										<a href = '/'>Профиль</a>
									</span>
								</div>
							</div>
						</div>
					</div>
				</div>
			</header>
		)
	}
}