import React, { Component } from 'react';

import UserSocialNetworksItem from './UserSocialNetworksItem';

export default class UserSocialNetworks extends Component {

	state = {
		socialNetworks: [
			{ width: 18, height: 18, name: 'google' },
			{ width: 24, height: 24, name: 'facebook' },
			{ width: 16, height: 16, name: 'instagram' }
		]
	};

	render() {
		const { socialNetworks } = this.state;
		const { makeLink } = this.props;

		return (
			<div className = 'user-info-social-networks'>
				{ socialNetworks.map((network, index) => {
					return <UserSocialNetworksItem key = { index }
									               name = { network.name }
									               width = { network.width }
									               height = { network.height }
									               makeLink = { makeLink } />
				}) }
			</div>
		)
	}
}