import React, { Component } from 'react';

import RatingsListItem from './RatingsListItem';

export default class RatingsList extends Component {

	state = {
		categories: [
			{ name: 'Гейминг', usersCount: 1, svgName: 'gaming', svgWidth: 24, svgHeight: 24  },
			{ name: 'Социальный', usersCount: 24, svgName: 'gaming', svgWidth: 24, svgHeight: 24 },
			{ name: 'Спорт', usersCount: 15, svgName: 'education', svgWidth: 24, svgHeight: 24 },
			{ name: 'Обучение', usersCount: 12, svgName: 'gaming', svgWidth: 24, svgHeight: 24 },
			{ name: 'Эрудиция', usersCount: 36, svgName: 'education', svgWidth: 24, svgHeight: 24 },
			{ name: 'Наука', usersCount: 120, svgName: 'gaming', svgWidth: 24, svgHeight: 24 },
			{ name: 'Гейминг', usersCount: 1, svgName: 'gaming', svgWidth: 24, svgHeight: 24  },
			{ name: 'Социальный', usersCount: 24, svgName: 'gaming', svgWidth: 24, svgHeight: 24 },
			{ name: 'Спорт', usersCount: 15, svgName: 'education', svgWidth: 24, svgHeight: 24 },
			{ name: 'Обучение', usersCount: 12, svgName: 'gaming', svgWidth: 24, svgHeight: 24 },
			{ name: 'Эрудиция', usersCount: 36, svgName: 'education', svgWidth: 24, svgHeight: 24 },
			{ name: 'Наука', usersCount: 120, svgName: 'gaming', svgWidth: 24, svgHeight: 24 }
		]
	};

	render() {
		const { categories } = this.state;
		const { beautifyCount } = this.props;

		return (
			<div className = 'ratings-list'>
				{ categories.map((category, index) => {
					return <RatingsListItem key = { index }
											name = { category.name }
											usersCount = { category.usersCount }
											svgName = { category.svgName }
											svgWidth = { category.svgWidth }
											svgHeight = { category.svgHeight }
											beautifyCount = { beautifyCount } />
				}) }
			</div>
		)
	}
}