import React, { Component } from 'react';

const mountainSvg = require('../../img/header/mountain.svg');
const messageSvg = require('../../img/header/message.svg');
const calendarSvg = require('../../img/header/calendar.svg');

export default class Menu extends Component {

	render() {
		return (
			<div className = 'menu'> 
				<div className = 'menu-item'>
					<a href = '/'>
						<span className = 'menu-item-icon'>
							<svg width = '24'
								 height = '16'
								 dangerouslySetInnerHTML = {{
								 	__html: `<use xlink:href="${mountainSvg}#mountain" />` 
								 }} />
						</span>
						<span className = 'menu-item-text'>Моя страница</span>
					</a>
				</div>

				<div className = 'menu-item'>
					<a href = '/'>
						<span className = 'menu-item-icon'>
							<svg width = '16'
								 height = '16'
								 dangerouslySetInnerHTML = {{
								 	__html: `<use xlink:href="${calendarSvg}#calendar" />` 
								 }} />
						</span>
						<span className = 'menu-item-text'>Лента</span>
					</a>
				</div>

				<div className = 'menu-item'>
					<a href = '/'>
						<span className = 'menu-item-icon'>
							<svg width = '18'
								 height = '18'
								 dangerouslySetInnerHTML = {{
								 	__html: `<use xlink:href="${messageSvg}#message"/>` 
								 }} />
						</span>
						<span className = 'menu-item-text'>Сообщения</span>
					</a>
				</div>
			</div>
		)
	}
}