import React, { Component } from 'react';

import RatingsSettingsDetailsItem from './RatingsSettingsDetailsItem';
import SearchArea from '../../SearchArea';
import CheckBoxArea from '../../CheckBoxArea';

export default class RatingsSettingsDetails extends Component {

	render() {
		const { typesDetails, arrowSvg } = this.props;

		return (
			<div className = 'ratings-settings-details'>
				{ typesDetails.map((detail, index) => {
					return <RatingsSettingsDetailsItem key = { index } 
													   name = { detail.name }
													   items = { detail.items }
													   arrowSvg = { arrowSvg } />
				}) }

				<div className = 'ratings-settings-details-item'> 
					<span className = 'ratings-settings-details-name'>Поиск</span>
					<SearchArea />
				</div>

				<div className = 'ratings-settings-details-item'> 
					<CheckBoxArea />
				</div>
			</div>
		)
	}
}