import React, { Component } from 'react';

export default class UserSocialNetworksItem extends Component {

	render() {
		const { width, height, name } = this.props;
		const link = require(`../../../img/main/${name}.svg`);

		return (
			<span className = 'user-info-social-networks-item'>
				<a href = '/'>
					<svg width = { `${width}px` }
						 height = { `${height}px` }
					 	 dangerouslySetInnerHTML = {{ 
					 	 	__html: `<use xlink:href="${link}#${name}" />`
					 	 }} />
				</a>
			</span>
		)
	}
}