import React, { Component } from 'react';

const userPhoto = require('../../../img/user-photo.jpg');

export default class RatignsTopUsersTableRow extends Component {

	render() {
		const { place, name, age, city, points, beautifyCount } = this.props;

		return (
			<tr className = 'ratings-top-users-table-body-line'>
				<td className = 'item place'>{ beautifyCount(place) }</td>

				<td className = 'item img'>
					<img src = { userPhoto }
					     alt = 'Фотография пользователя' />
				</td>

				<td className = 'item name'>{ name }</td>
				<td className = 'item age'>{ age }</td>
				<td className = 'item city'>{ city }</td>
				<td className = 'item points'>{ points }</td>
			</tr>
		)
	}
}