import React, { Component } from 'react';

import SearchArea from '../../SearchArea';
import RatingsList from './RatingsList';

const plusSvg = require('../../../img/main/plus.svg');
const expandSvg = require('../../../img/main/expand.svg');

export default class Ratings extends Component {

	state = {
		ratingsCount: this.props.beautifyCount(941)
	};

	render() {
		const { ratingsCount } = this.state;
		const { beautifyCount } = this.props;
		const searchAreaStyle = { marginTop: '10px' };

		return (
			<div className = 'ratings'>
				<div className = 'ratings-title'>
					<span className = 'ratings-title-text'>Рейтинги</span>
					<span className = 'ratings-title-count'>Всего { ratingsCount }</span>
				</div>

				<SearchArea placeholder = 'Поиск рейтинга...' style = { searchAreaStyle } />

				<div className = 'ratings-options'>
					<span className = 'ratings-options-item'>
						<a href = '/'>
							<span className = 'ratings-options-item-text'>
								Добавить еще
							</span>
							<svg width = '23'
								 height = '20'
								 dangerouslySetInnerHTML = {{
								 	__html: `<use xlink:href="${plusSvg}#plus" />` 
								 }} />
						</a>
					</span>

					<span className = 'ratings-options-item'>
						<a href = '/'>
							<span className = 'ratings-options-item-text'>
								Смотреть всем
							</span>
							<svg width = '21'
								 height = '18'
								 dangerouslySetInnerHTML = {{
								 	__html: `<use xlink:href="${expandSvg}#expand" />` 
								 }} />
						</a>
					</span>
				</div>

				<RatingsList beautifyCount = { beautifyCount } />
			</div>
		)
	}
}