import React, { Component } from 'react';

const searchSvg = require('../img/search.svg');
const crossSvg = require('../img/cross.svg');

export default class SearchArea extends Component {

	state = {
		searchActive: false,
		userInput: ''
	}

	handleUserFocus = () => this.setState({ searchActive: true });

	handleUserBlur = () => this.setState({ searchActive: false });

	handleUserChange = event => this.setState({ userInput: event.target.value.trim() });

	clearUserInput = () => this.setState({ userInput: '' });

	render() {
		const { searchActive, userInput } = this.state;
		const { style, placeholder = 'Поиск...' } = this.props;
		
		return (
			<div className = { searchActive ? 'search-area active' : 'search-area' } style = { style }>
				<input className = 'search-area-input' 
					   placeholder = { placeholder }
					   value = { userInput }
					   onFocus = { this.handleUserFocus }
					   onBlur = { this.handleUserBlur } 
					   onChange = { this.handleUserChange } />
				<svg width = '20'
					 height = '20'
					 className = 'search-svg'
					 dangerouslySetInnerHTML = {{
					 	__html: `<use xlink:href="${searchSvg}#search" />` 
					 }} />
				{ userInput !== '' ?
				  <svg width = '12'
				       height = '12'
				  	   className = 'cross-svg'
				  	   onClick = { this.clearUserInput }
				  	   dangerouslySetInnerHTML = {{
				  	       __html: `<use xlink:href="${crossSvg}#cross" />` 
				  	   }} /> : null }
			</div>
		)
	}
}