import React, { Component } from 'react';

import RatingsTopBestUsers from './RatingsTopBestUsers';
import RatingsTopUsers from './RatingsTopUsers';

export default class RatingsTop extends Component {

	state = {
		users: [
			{ name: 'Криковец Арина', 
			  age: 20, 
			  city: 'Санкт-Петербург', 
			  email: 'kate@mail.ru',
			  points: this.props.beautifyCount(9413807) },
			{ name: 'Криковец Арина', 
			  age: 20, 
			  city: 'Санкт-Петербург', 
			  email: 'kate@mail.ru',
			  points: this.props.beautifyCount(94138) },
			{ name: 'Криковец Арина', 
			  age: 20, 
			  city: 'Санкт-Петербург', 
			  email: 'kate@mail.ru',
			  points: this.props.beautifyCount(9413) },
			{ name: 'Криковец Арина', 
			  age: 20, 
			  city: 'Санкт-Петербург', 
			  email: 'kate@mail.ru',
			  points: this.props.beautifyCount(941) },
		],

		usersCount: this.props.beautifyCount(120000),
		sortType: 'вверх ↑'
	};

	changeSortType = () => {
		const { sortType } = this.state;
		const newType = sortType === 'вверх ↑' ? 'вниз ↓' : 'вверх ↑';

		this.setState({ sortType: newType });
	};

	render() {
		const { beautifyCount } = this.props; 
		const { users, usersCount, sortType } = this.state;
		const bestUsers = users.slice(0, 3);

		return (
			<div className = 'ratings-top'>
				<div className = 'ratings-top-info'>
					<span className = 'ratings-top-info-title'>Рейтинг</span>
					<span className = 'ratings-top-info-users-count'>Найдено { usersCount } участников</span>
					<span className = 'ratings-top-info-sort' onClick = { this.changeSortType }>
						Сортировать 
						<span className = 'ratings-top-info-sort-type'>{ sortType }</span>
					</span>
				</div>

				<RatingsTopBestUsers bestUsers = { bestUsers } />
				<RatingsTopUsers beautifyCount = { beautifyCount } users = { users } />
			</div>
		)
	}
}