import React, { Component } from 'react';

export default class UserStatisticItem extends Component {

	render() {
		const { name, value } = this.props;

		return (
			<span className = 'user-info-statistic-item'>
				<span className = 'user-info-statistic-item-count'>{ value }</span>
				<span className = 'user-info-statistic-item-name'>{ name }</span>
			</span>
		)
	}
}