import React, { Component } from 'react';

export default class RatingsDisclaimer extends Component {

	state = {
		ratingName: 'Гейминг',
		ratingDescription: 'Этот рейтинг сравнивает игровые показатели по играм из Steam и Origin.',
		ratingUserCount: this.props.beautifyCount(4941)
	};

	render() {
		const { ratingName, ratingDescription, ratingUserCount } = this.state; 

		return (
			<div className = 'ratings-disclaimer'>
				<div className = 'ratings-disclaimer-info'>
					<span className = 'ratings-disclaimer-info-title'>{ ratingName }</span>
					<span className = 'ratings-disclaimer-info-text'>{ ratingDescription }</span>
				</div>

				<span className = 'ratings-disclaimer-users-count'>
					{ ratingUserCount } участник
				</span>
			</div>
		)
	}
}