import React, { Component } from 'react';

import UserStatisticItem from './UserStatisticItem';

export default class UserStatistic extends Component {

	state = {
		statistic: [
			{ name: 'Подписчики', value: this.beautifyUserStatistic('12000000') },
			{ name: 'Подписки', value: this.beautifyUserStatistic('15000') },
			{ name: 'Рейтинг', value: this.beautifyUserStatistic('50') }
		]
	};

	beautifyUserStatistic(statistic) {
		if (statistic.length < 4) return statistic;

		const shortValueName = statistic.length >= 6 ? 'm' : 'k'
		statistic = statistic.split('');

		if (statistic.length >= 6) statistic.splice(statistic.length - 6, 0, '.');
		else statistic.splice(statistic.length - 3, 0, '.');

		statistic = statistic.join('').replace(/\b.0+/, '').split('');

		if (statistic[statistic.length - 1] === '.') statistic.pop();

		return statistic.join('') + shortValueName;
	}

	render() {
		const { statistic } = this.state;

		return (
			<div className = 'user-info-statistic'>
				{ statistic.map((item, index) => {
					return <UserStatisticItem key = { index } name = { item.name } value = { item.value } />
				}) }
			</div>
		)
	}
}