import React, { Component } from 'react';

export default class CheckBoxArea extends Component {

	state = {
		boxes: [
			{ name: 'only-my-friends', text: 'Только мои друзья', selected: false },
			{ name: 'show-rating-changes', text: 'Показать изменения рейтинга', selected: false }
		]
	};	

	selectBox = event => {
		const boxes = Object.assign([], this.state.boxes);
		const input = event.target;
		const box = boxes.find(box => box.name === input.name);

		boxes[boxes.indexOf(box)].selected = !boxes[boxes.indexOf(box)].selected;

		this.setState({ boxes: boxes });
	}

	render() {
		const { boxes } = this.state;

		return (
			<div className = 'checkbox-area'>
				{ boxes.map((box, index) => {
					return <div key = { index } className = 'checkbox-area-item'>
						       <input onChange = { this.selectBox }  
						       		  type = 'checkbox' 
						       		  name = { box.name }
						       		  id = { box.name } />
						       <label htmlFor = { box.name }>{ box.text }</label>
						   </div>
				}) }
			</div>
		)
	}
}