import React, { Component } from 'react';

const userPhoto = require('../../../img/user-photo.jpg');

const placesImg = [require('../../../img/main/first.png'), 
				   require('../../../img/main/second.png'), 
				   require('../../../img/main/third.png')];


export default class RatingsTopBestUsers extends Component {

	render() {
		const { bestUsers } = this.props;

		return (
			<div className = 'ratings-top-best'>
				{ bestUsers.map((user, index) => {
					return <div key = { index } className = 'ratings-top-best-item'>
							   <img src = { userPhoto }
							   		className = 'ratings-top-best-user-photo' 
							   		alt = 'Фотография пользователя' />

						       <div className = 'ratings-top-best-user-info'>
						           <span className = 'ratings-top-best-user-info-name'>{ user.name }</span>
						           <span className = 'ratings-top-best-user-info-email'>{ user.email }</span>
						       </div>

						       <div className = 'ratings-top-best-points'>
						           <span className = 'ratings-top-best-points-count'>{ user.points } очков</span>
						           <img src = { placesImg[index] } 
						           		className = 'ratings-top-best-points-place'
						           		alt = 'Фотография места' />
						       </div>
						   </div>
				}) }
			</div>
		)
	}
}