import React, { Component } from 'react';

import RatingsSettingsType from './RatingsSettingsType';
import RatingsSettingsDetails from './RatingsSettingsDetails';

const arrowSvg = require('../../../img/main/arrow.svg');

export default class RatingsSettings extends Component {

	state = {
		types: ['Рейтинговые настройки', 'Рейтинговые настройки', 'Рейтинговые настройки'],
		typesDetails: [
			{ name: 'Игра', 
			  items: [
			      'Counter-Strike: Global Offensive', 
			 	  'Counter-Strike: Global Offensive',
			 	  'Counter-Strike: Global Offensive',
			 	  'Counter-Strike: Global Offensive',
			 	  'Counter-Strike: Global Offensive'
			 ] },

			{ name: 'Период', 
			  items: [
			      'За последние 3 месяца', 
			 	  'За последние 3 месяца',
			 	  'За последние 3 месяца',
			 	  'За последние 3 месяца',
			      'За последние 3 месяца'
			 ] }
		]
	};

	render() {
		const { types, typesDetails } = this.state;

		return (
			<div className = 'ratings-settings'>
				<RatingsSettingsType types = { types } arrowSvg = { arrowSvg } />
				<RatingsSettingsDetails typesDetails = { typesDetails } arrowSvg = { arrowSvg } />
			</div>
		)
	}
}