import React, { Component } from 'react';

export default class RatingsSettingsDetailsItem extends Component {

	render() {
		const { name, items, arrowSvg } = this.props;

		return (
			<div className = 'ratings-settings-details-item'> 
				<span className = 'ratings-settings-details-name'>{ name }</span>

				<div className = 'ratings-settings-details-list'>
					{ items.map((item, index) => {
					    return (
						    <span key = { index } 
								  className = 'ratings-settings-details-list-item'>
								{ item }

								{ index === 0 && 
								  <svg width = '12'
								       height = '9'
								       dangerouslySetInnerHTML = {{
								           __html: `<use xlink:href="${arrowSvg}#arrow" />` 
								       }} />
								}
							</span>
						)
					}) }
				</div>
			</div>
		)
	}
}